const Menu = props => {
  const optionSelectedLength = props.getValue().length || 0;
  return (
    <components.Menu {...props}>
      {optionSelectedLength < 5 ? (
        props.children
      ) : (
        <div style={{ margin: 15 }}>Vous avez atteint le nombre maximum de mot-clés</div>
      )}
    </components.Menu>
  );
};

