    patch=(data)=>{
      var id=localStorage.getItem("idexpert")
      const requestOptions = {
          method: 'PATCH',
          headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
          body: JSON.stringify(data)
      };
      fetch(url+"utilisateurs/"+id, requestOptions)
      .then((response) => response.json())
      .then(response =>{console.log("ok,",response)
      this.setState({message:"Elément modifier avec succès!!"})
      this.setState({show:true,success:true});
      setTimeout(()=>
        window.location.reload(false)
      , 1000)
      
      //this.handleChargeAll()
      $(".owener").hide()
      } ,
      (error) => {
        $(".owener").hide()
      })
    }

    remove=(data,Url)=>{
      
      const requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
          body: JSON.stringify(data)
      };
    
        fetch(url+Url,requestOptions)
        .then((response) => response.json())
        .then(response =>{console.log("ok,",response)
        this.setState({message:"Elément supprimer"})
        this.setState({show:true,success:true});
        
    
        } ,
        (error) => {
          console.log("erreur,",error)
        })
    }

   
