
import React, { Component,Suspense } from "react"
import PropTypes from 'prop-types'
import MetaTags from 'react-meta-tags';

import { MDBDataTable } from "mdbreact"

import { format } from 'date-fns';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
  Spinner
} from "reactstrap"

import Select from "react-select"
import {component} from "react-select"
import axios from 'axios'
import  { Redirect,useHistory } from 'react-router-dom'
// import $ from "jquery";import { useHistory } from "react-router-dom";

import Flatpickr from "react-flatpickr"
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import url from "../../common/api"
//import BootstrapSelect from "bootstrap-select"

class ListeExpert extends Component {

  
  constructor(props) {
    super(props)
    this.state = {
      ok:"",
      expertData:[],
      expert:[],
      chargement:false,
      profile:JSON.parse(localStorage.getItem("profileAuthAshia")),
       
    }
    this.handleDetail.bind(this)
  }
  componentDidMount() {

    this.handleChargerData()

     

  }

  handleDetail=(id)=>{
    localStorage.setItem('idexpert',id);
    this.setState({ok: <Redirect to='/detail-expert'  />});
  }


  handleChargerData=()=>{
    var data=JSON.parse(localStorage.getItem("authAshia")).profile
   
   fetch(url+"profiles/"+data.id)
      .then((response) => response.json())
      .then((data) => {
        localStorage.setItem("profileAuthAshia", JSON.stringify(data))
        this.setState({expertData:data.utilisateurs,chargement:true})
        data.utilisateurs.filter(e=>e.fonction=="EXPERT").map((element,idx) =>{
          var tab= {
            nom: element.nom,
            //thematique: element.nom,
            prenom: element.prenom,
            //format: element.format.code,
            //langue: element.nom,
            email: element.email,
            //typesystemeirrigue: element.nom,
            action: <span style={{backgroundColor:"#5FC4E1"}} className="badge rounded-pill"> <a onClick={this.handleDetail.bind(this.bind,element.id)} >Consulter</a></span>,
            // action: <a > <i onClick={this.handleDetail.bind(this.bind,element.id)} style={{color:"#5FC4E1"}} className="bx bxs-info-circle bx-sm "/> </a>,
            
          }
         
          this.setState({expert:[...this.state.expert,tab]})
        })
      
        
        
       }
       
      );
  }

  handleAjoutExpert=()=>{
    this.setState({ok: <Redirect to='/ajout-expert'  />});
  }

  loading = () => <Spinner animation="grow" />

  render() {

    const data = {
      columns: [
        {
          label: "Nom",
          field: "nom",
          sort: "asc",
          width: 150,
        },
        {
          label: "Prénom",
          field: "prenom",
          sort: "asc",
          width: 270,
        },
        {
          label: "Email",
          field: "email",
          sort: "asc",
          width: 270,
        },
      
        {
          label: "Détail",
          field: "action",
          sort: "asc",
          width: 100,
        },
      ],
       rows:_.sortBy(this.state.expert, 'created_at').reverse()
      //rows:this.state.expert
    }


    return (
      <React.Fragment>
        <div className="page-content"> {this.state.ok}
           
          
          <MetaTags>
            <title >Liste des acteurs de l'irrigation</title>
          </MetaTags>

          <Container fluid={true}>
            {/* <h1 onClick={this.goBack}>Go</h1> */}
            <Row>
              <Col md="8">
                <div className="page-title-box d-flex align-items-center justify-content-between">
                  {/* <h4 className="mb-0 font-size-18">Ressources</h4> */}
                </div>
              </Col>
            </Row>

            <Card style={{borderRadius:"65px",borderColor:"#BBB78B",borderWidth:"5px"}}>
              <CardBody>
                {/* <label><strong>RECHERCHE</strong></label> */}
                <Row>
                  <Col lg="6" md="6" sm="12" className="mb-2">
                    <div className="form-outline" class="form">
                      <input
                        // onChange={this.handleRechercheSimple}
                        type="search"
                        className="form-control "
                        placeholder="Saisissez votre recherche..."
                        style={{borderRadius:"40px"}}
                      />
                    </div>
                  </Col>
                  {this.state.profile?.droit_expert?.creer?
                  <Col lg="6" md="6" sm="12" className="pull-right">
                    <Button
                      color="success"
                      className="font-16 btn-block btn   float-right"
                      onClick={this.handleAjoutExpert}
                     
                      // style={{backgroundColor:"#6A7172"}}
                      style={{backgroundColor:"#aad29a", borderColor:"#aad29a",float:"right",borderRadius:"40px"}}
                    >
                      <i className="mdi mdi-plus-circle-outline me-1" />
                      Ajouter un nouvel acteur
                    </Button>
                  </Col>:null
                  }
                </Row>
              </CardBody>
            </Card>


            {/* Types d'acteurs */}
            <Row>
              <h5 class="heading-1 text-center"><span>Types d'acteurs</span></h5>
              <Col lg="4" md="6" sm="12" className="mt-2" >
                <Card className="block" id="block" style={{borderRadius:"25px",borderColor:"#BBB78B",borderWidth:"5px"}}>
                  <CardBody>
                    <CardTitle className="h4 mb-3">
                      <Col ><h4><span style={{backgroundColor:"#6A7172"}} className="badge rounded-pill ">Test</span></h4></Col> 
                      {/* <Col><img src={iconesis} style={{float:"right"}} class="rounded mx-auto d-block" width="50" height="50" alt="..."/></Col> */}
                      {/* <Col> <button type="button" style={{backgroundColor:"#5FC4E1",float:"right",cursor:"pointer",borderRadius:"20px"}} className="btn btn-info btn-elegant" > <i className="fas fa-eye mr-1"></i></button></Col> */}
                    </CardTitle>
                    {/* <img src={iconesis} class="rounded mx-auto d-block" width="60" height="60" alt="..."/> */}
                    Nombre d'acteurs : <span style={{backgroundColor:"#6A7172"}} className="badge rounded-pill ">Taille</span>
                  </CardBody>
                </Card>
              </Col>
            </Row>

            {/* Sous type d'acteurs */}
            <Row className="text-center justify-content-center">
              <>
              <h5 class="heading-1 text-center"><span>Sous-types d'acteurs</span></h5>
              <Col lg="3" md="4" sm="12" className="mt-2" >
                <Card className="block" id="block" style={{borderRadius:"70px",borderColor:"#BBB78B",borderWidth:"5px"}}>
                  <CardBody>
                    Test <span style={{backgroundColor:"#6A7172"}} className="badge rounded-pill ">Taille</span>
                  </CardBody>
                </Card>
              </Col>
              </>
              <hr/>
            </Row>


            <Row>
              <Col className="col-12 mt-4">
              {
                (this.state.chargement)?
       
                <Card style={{borderRadius:"25px",borderColor:"#6A7172",borderWidth:"5px"}}>
                  <CardBody>
                    <CardTitle className="h4 mb-5">
                      LISTE DES ACTEURS 
                    </CardTitle>

                    <p className="card-title-desc">
                      {/* Description du tableau. */}
                    </p>
                    <Suspense fallback={this.loading()}>
                      <MDBDataTable responsive striped bordered style={{textAlign:"justify"}} data={data} />
                    </Suspense>
                  </CardBody>
                </Card>
                :(
                  <div style={{marginTop:"150px",textAlign:"center"}}>
                    <Spinner type="grow" color="secondary" />
                 </div>
                )
              }
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}
export default ListeExpert