  handleChargeFile=()=>{
    //this.setState({chargement:false})
    var id=localStorage.getItem("idressource")
    fetch(url+"ressources/"+id)
    .then((response) => response.json())
    .then((data) => {
        $(".owener").show()
        data.images.map(
            (element,idx)=>{
                this.setState({image:[...this.state.image,{id:element.id,name:element.file,file:urlFile.image+"/"+element.file}]})
            }
        )
        data.pjointes.map(
            (element,idx)=>{

                taille=parseInt(element.taille)/1024
                this.setState({pjointe:[...this.state.pjointe,{id:element.id,name:element.file,taille:taille+" KO",nom:element.file.split('.')[0].split('-')[1],file:urlFile.image+"/"+element.file,type:element.file.split('.')[1]}]})
               
            }
        )
    })

  }

  handleVue=(data)=>{
    
    
    const requestOptions = {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
        body: JSON.stringify(data)
    };

    var id=localStorage.getItem("idressource")
    fetch(url+"ressources/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      
    })

  }


  componentDidMount(){
    
    var id=localStorage.getItem("idressource")
    $(".owener").hide()
    $(".owener2").hide()
    $("#profile").hide()
    if(this.state.index===true){
      $("#droit").hide()
    }
    this.setState({hide:"none"})
    var taille=0
   
    //get list
    getFormatList().then(e=>this.setState({formats:e}))
    getTypeRessourceList().then(e=>this.setState({type_ressources:e}))


    axios.get(url+"ressources/"+id)
    //.then((response) => response.json())
    .then(res => {
        const data=res.data
     
        this.setState({chargement:true})
        this.handleDroit()
        this.setState({ressource:data})
        console.log("ress",data)
      
        data.type_ressource.sous_type_ressources.map((el)=>{
            this.setState({sous_type_ressources:[...this.state.sous_type_ressources,{value:el.id,label:el.libelle}]})
        })
        this.setState({systeme:data.type_systeme_irrigues,sous_type_ressource:data.sous_type_ressources})
        this.setState({langue:data.langues})
        this.setState({acces:data.profiles})
        this.setState({unite:data.unite_administratives})
        this.setState({thematique:data.thematiques})
        this.setState({mot:data.mot_cles})
        this.setState({auteur:data.auteurs})
        this.setState({localite:data.pays})
        this.setState({url:data.url})
        this.setState({defaultDateDebut:Date.parse(data.date_debut_publication.split("/")[2]+"-"+data.date_debut_publication.split("/")[0]+"-"+data.date_debut_publication.split("/")[1])})
        this.setState({defaultDateFin:Date.parse(data.date_fin_publication.split("/")[2]+"-"+data.date_fin_publication.split("/")[0]+"-"+data.date_fin_publication.split("/")[1])})


        data.pays.map((val,idx)=>{
          val.unite_administratives.map((el,id)=>{
            this.setState({unites:[...this.state.unites,{value:el.id,label:el.libelle}]})
          })
        })



        // nombre de vue
        this.handleVue( {
            nombre_vue:parseInt(data.nombre_vue)+1
        } )


        $("#nom").val(data.nom)
        $("#url").val(data.url)
        $("#description").val(data.description)

        $("#date_debut").val(data.date_debut_publication)
        $("#date_fin").val(data.date_fin_publication)

        $("#date_edition").val(data.date_edition)

        const date1= data.date_edition.split("T")[0]
        const date2= data.date_fin_publication.split("T")[0]

        const dateD=date1.split("-")[2]+"/"+date1.split("-")[1]+"/"+date1.split("-")[0]
        const dateF=date2.split("-")[2]+"/"+date2.split("-")[1]+"/"+date2.split("-")[0]

         this.setState({dateD:dateD,date_edition:data.date_edition,dateF:dateF})
       
        if(data.video==""){
          //$("#video").css("display","none")
        }

        if(JSON.parse(localStorage.getItem("authAshia")).id!=data.utilisateur.id){
            $("#droit").hide()
        }
        if(this.state.index===true){
          $("#droit").hide()
        }

        data.pjointes.map(
            (element,idx)=>{
                
                taille=parseInt(element.taille)/1024
                this.setState({pjointe:[...this.state.pjointe,{id:element.id,name:element.file,taille:taille+" KO",nom:element.file.split('.')[0].split('-')[1],file:urlFile.image+"/"+element.file,type:element.file.split('.')[1]}]})
                // if(parseInt(element.taille)<1024){
                //   taille=parseInt(element.taille)/1024
                //   this.setState({pjointe:[...this.state.pjointe,{taille:taille+"KO",nom:element.file.split('.')[0].split('-')[1],file:"http://localhost/SIS/fichiers/data/"+element.file,type:element.file.split('.')[1]}]})
                // }
                // if(parseInt(element.taille)>=1024){
                //     taille=parseInt(element.taille)/1024
                //     this.setState({pjointe:[...this.state.pjointe,{taille:taille+"MO",nom:element.file.split('.')[0].split('-')[1],file:"http://localhost/SIS/fichiers/data/"+element.file,type:element.file.split('.')[1]}]})
                //   }
               

               
            }
        )

        data.images.map(
            (element,idx)=>{
                this.setState({image:[...this.state.image,{id:element.id,name:element.file,file:urlFile.image+"/"+element.file}]})
            }
        )
        try{
         $("#format").val(data.le_format.libelle)
        }catch(e){

        }
        try{
            $("#type_ressource").val(data.type_ressource.libelle)
           }catch(e){
   
           }
        try{
            $("#format").val(data.format.libelle)
           }catch(e){
   
        }   
        try{
            this.setState({video:data.video.replace("watch?v=","embed/")})
           }catch(e){
   
        }
      })

      //chargement des données
    this.handleCharge()
  }

  
