  handleCharge=()=>{

    this.handleChargeMotCle()
    this.handleChargeAuteur()
    this.handleChargeLangue()
    this.handleChargeLocalisation()
    this.handleChargeSysteme()
    // this.handleChargeUnite()
    this.handleChargeThematique()

  

    getQueries("profiles").then(e=>{
      const data=e
      try{
        this.setState({public_id:data.filter(x => (x.libelle).toLowerCase() === ("Public").toLowerCase())[0].id,
        profiles_attributes:[{"profile_id":data.filter(x => (x.libelle).toLowerCase() === ("Public").toLowerCase())[0].id}]
      })
        
       }catch(error){
  
       }
       var pub=data.filter(x => (x.libelle).toLowerCase() === ("Public").toLowerCase())
       let acces= _.sortBy(this.state.acces.concat(pub), 'id')

       let pay1 = acces.map(val => { return val.id});
       
       let pay2 = data.filter(itemX => !pay1.includes(itemX.id));
     
       pay2.map((element,idx) =>{
        this.setState({profile:[...this.state.profile,{value:element.id,label:element.libelle}]})
      })

    

    })
  
  
  }



