    handleUpdateRessource=()=>{
      var imageList=document.getElementById("image").files
      var pjList=document.getElementById("pieceJointe").files
       this.setState({chargement_data:true})

       console.log("sous ressource",this.state.checkSousTypeRessource)
      // save new pjointe
      for(var i=0;i<pjList.length;i++){
        const formData = new FormData(); 
      
        formData.append( 
          "avatar", 
          pjList[i] 
         
        ); 
        updatePjointe(formData).then(data=>{

        })
      }

// supprimer les elements concernés
      this.handleDeleteActions()


      var ressource={
        "description":$("#description").val(),
        "video":$("#video").val(),
        "url":$("#url").val(),
        "nom":$("#nom").val(),
        "date_edition":this.state.date_edition,
        "ressource_auteurs_attributes":_.uniqBy(this.state.checkAuteur, 'auteur_id'),
        "ressource_thematiques_attributes":_.uniqBy(this.state.checkThematique, 'thematique_id'),
        "ressource_pays_attributes":_.uniqBy(this.state.checkLocalisation, 'pay_id'),
        "ressource_unite_administratives_attributes":_.uniqBy(this.state.checkUnite, 'unite_administrative_id'),
        "ressource_langues_attributes":_.uniqBy(this.state.checkLangue, 'langue_id'),
        "ressource_mot_cles_attributes":_.uniqBy(this.state.checkMot, 'mot_cle_id'),
        "ressource_sous_ressources_attributes":_.uniqBy(this.state.checkSousTypeRessource, 'sous_type_ressource_id'),
        "ressource_systeme_irrigues_attributes":_.uniqBy(this.state.checkSysteme, 'type_systeme_irrigue_id'),
        "ressource_profiles_attributes":_.uniqBy(this.state.checkAcces, 'profile_id')
      }

      if(this.state.checkNewMot.length){
       
        addMotCle(this.state.checkNewMot,localStorage.getItem("idressource"))
      }

      
      if(this.state.checkNewAuteur.length){
        
        addAuteur(this.state.checkNewAuteur,localStorage.getItem("idressource"))
      }



      if($("#show-format option:selected").val()!=="0"){
        ressource["le_format_id"]=$("#show-format option:selected").val()
      }
     
      
      if($("#show-type-ressource option:selected").val()!=="0"){
        ressource["type_ressource_id"]=$("#show-type-ressource option:selected").val()

        
      if($("#show-type-ressource option:selected").val()!==this.state.ressource?.type_ressource?.libelle){
        removeSousTypeInRessource(this.state.sous_type_ressource,this.state.idRessource).then(data=>{

        })
      }
     
        
      }

      
      // removeUniteAdministrativeInRessource(this.state.unite,this.state.idRessource).then(data=>{

      // })

      
    

      for(var i=0;i<imageList.length;i++){
        const formData = new FormData(); 
         
        // Update the formData object 
        formData.append( 
          "avatar", 
          imageList[i] 
         
        );
        if(i==imageList.length-1){

          //update ressource when image exist
        updateImage(formData).then(data=>{
          updateRessource(ressource).then(data=>{

            this.handleVue( {
              nombre_vue:parseInt(data.nombre_vue)-1
          } )

            this.setState({message:"La ressource a été modifiée avec succès !"})
            this.setState({show:true,success:true});
            setTimeout(()=>
              window.location.reload(false)
            , 2000)
          })
        })
      }else{
        updateImage(formData).then(data=>{
            
        })
      }
      
    }

      //update ressource width image
     if(!imageList.length){
      updateRessource(ressource).then(data=>{
        this.handleVue( {
          nombre_vue:parseInt(data.nombre_vue)-1
      } )
        this.setState({message:"La ressource a été modifiée avec succès !"})
        this.setState({show:true,success:true});
        setTimeout(()=>
          window.location.reload(false)
        , 2000)
      })
    }
  }

