        "video": $("#video").val(),
        //"image": "img",
        "description": $("#description").val(),
        "url": $("#url").val(),
        "utilisateur_id":JSON.parse(localStorage.getItem("authAshia")).id ,
        "le_format_id":$("#format_id option:selected").val(),
        "type_ressource_id":$("#type_ressource_id option:selected").val(),
        "ressource_profiles_attributes":this.state.profiles_attributes,
