  onChangeFile=(e)=>{
    var file=e.target.files
    console.log("LE FICHIER",file)
    try{
      this.setState({fileok:true})
      
      for(var i=0;i<file.length;i++){
        console.log("ff-file-size",file[i].size)
        if(parseInt(file[i].size)>2100612){
        //if(parseInt(file[i].size)>10000000){
          $("#pieceJointe").addClass("is-invalid")
          $("#valideFile").show()
          this.setState({fileok:false})
        }else{
          $("#pieceJointe").removeClass("is-invalid")
          $("#valideFile").hide()
          this.setState({fileok:true})
          // e.target.value = null
        }
      }
    }catch(error){console.log("ff-error",error)}
  }
 
