  handleErrorNom=(e)=>{
    if(e.target.value!=0){
      $("#valideNom").hide()
      $("#nom").removeClass("is-invalid")
      $("#echecSauv").hide()
    }else{
      $("#valideNom").show()
      $("#nom").addClass("is-invalid")
    }
  }
  handleErrorPrenom=(e)=>{
    if(e.target.value!=0){
      $("#validePrenom").hide()
      $("#prenom").removeClass("is-invalid")
      $("#echecSauv").hide()
    }else{
      $("#validePrenom").show()
      $("#prenom").addClass("is-invalid")
    }
  }
  handleErrorTypeExpert=(e)=>{
    var data=[]
    var type=[]
    this.setState({sous_type_ressource:[]})
   
    if(e.target.value!=0){
      
      this.state.type_expert.find(val=>val.value==e.target.value)?.sous_types.map((el)=>{
        type=[...type,{value:el.id,label:el.libelle}]
        this.setState({sous_type_expert:type})
      })

      $("#valideTypeExpert").hide()
      $("#type_expert_id").removeClass("is-invalid")
      $("#echecSauv").hide()
    }else{
      $("#valideTypeExpert").show()
      $("#type_expert_id").addClass("is-invalid")
    }
  }
  handleErrorEmail=(e)=>{
    var email = e.target.value
    if(validator.isEmail(email)) {
      $("#valideEmail").hide()
      $("#email").removeClass("is-invalid")
      $("#echecSauv").hide()
    }else{
      $("#email").addClass("is-invalid")
      $("#valideEmail").show()
      
    }
  }
  onChangeBlocAcces=(e)=>{
    
    this.setState({block_acces:$('.chbx').is(':checked')})
  }

  validate(){
    let input = this.state.input;
    let errors = {};
    let isValid = true;

    // if (!input["email"]) {
    //   isValid = false;
    //   errors["email"] = "Please enter your email Address.";
    // }

    if (typeof input["email"] !== "undefined") {
      var pattern = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);
      if (!pattern.test(input["email"])) {
        isValid = false;
        errors["email"] = "Please enter valid email address.";
      }
    }

    this.setState({
      errors: errors
    });

    return isValid;
  }
