handleFiltreAvanceLocalisation = (e) => {
  var data=[]
  var ressource=[]
  var unite=[]
  var expert=[]
  var event=[]
  this.setState({unite:[],expert_pays:data,evenement_pays:data})
  try{
    
    e.map((element,idx)=>{
      data=data.concat(element.ressources)
      console.log("pays===>",element)
      element.unite_administratives.map((el)=>{
        unite=[...unite,{value:el.id,label:el.libelle,ressources:el.ressources}]
      
        this.setState({unite:unite})
      })

      if(idx==e.length-1){
        var duplicates=  _.uniqBy(data, 'id')
        expert=_.uniqBy(expert.concat(element.utilisateurs), 'id')
        event=_.uniqBy(event.concat(element.evenements), 'id')

        let result = duplicates.filter((e, i) => {
          return duplicates.findIndex((x) => {
          return x.id == e.id }) == i;
      
        });

        this.setState({ressource_localisation:result,expert_pays:expert,evenement_pays:event,localisation_choose:true})
      }
     
    })
    
   
    
  }catch(ee){
    
    this.setState({localisation_choose:false,ressource_localisation:ressource})
  }

}

